@import '../../styles/variables.scss';

.header {
    display: flex;
    justify-content: space-between;
    position: sticky;
    top: 0;
    width: 100%;
    z-index: 1;
    height: 70px;
    padding: 5px 0 5px 0;
    background-color: $primary-color;

    &__navbar {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 0px;
        margin: 0;
        width: 100%;
        height: 55px;
        list-style-type: none;
        @media (min-width: 768px) {
            width: 95%;
        }
        &--li {
            opacity: 0;
            @media (min-width: 768px) {
                opacity: 1;
                font-size: 1.4em;
            }
            & a {
                text-decoration: none;
                color: $third-color;
                font-size: 0.9em;
                padding: 5px;
            }
            & .active {
                border-radius: 20px;
                background-color: $third-color;
                color: $primary-color;
            }
        }
    }
}

#iconHome {
    opacity: 1;
    padding-top: 5px;
    display: flex;
    flex-direction: row;
    width: 50%;
    margin-left: 5px;
    & .active {
        border-radius: 20px;
        border: solid 2px $third-color;
        background-color: transparent;
    }
    & > img {
        height: 45px;
        width: 51px;
    }
}
.iconNavbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
#btnLanguage {
    display: flex;
    margin: 20px 5px 0 10px;
}
