@import '../../styles/variables.scss';

.project {
    display: flex;
    flex-direction: column;
    align-items: center;
    &.dark-mode {
        background: $secondary-color-dark;
    }
    &__banner {
        display: flex;
        justify-content: center;
        background: linear-gradient(to top, $secondary-color, $third-color);
        border-radius: 70% 0 0 0;
        @media (min-width: 768px) {
            width: 60%;
        }
        @media (min-width: 1440px) {
            width: 45%;
        }
        & img {
            margin: 40px 40px 10px 40px;
            width: 80%;
            @media (min-width: 768px) {
                width: 90%;
                height: 250px;
            }
        }
    }
    &__title {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 95%;
        @media (min-width: 768px) {
            width: 70%;
        }
        & a > img {
            width: 80%;
            height: 80%;
        }
        & > .title {
            font-size: 1.5em;
            text-align: center;
            width: 80%;
            background-size: 90% 90%;
            @media (min-width: 768px) {
                width: 60%;
                font-size: 1.8em;
                margin: 60px 20px 60px 20px;
            }
            @media (min-width: 1440px) {
                width: 40%;
                background-size: 80%;
            }
        }
    }
    &__tags {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        & .tag {
            color: $primary-color;
            background: linear-gradient(to top, $secondary-color, $third-color);
            width: 40%;
            text-align: center;
        }
    }
}
